import Axios from "axios";
import React, { Component } from "react";
import { gagal } from "../Alert";
import FormStok from "./Component/FormStok";
import laporanPrint from "./Component/laporanPrint";
import laporanPrintKirimMasak from "./Component/laporanPrintKirimMasak";
import laporanPrintMutasi from "./Component/LaporanPrintMutasi";
import laporanPrintTerimaMasak from "./Component/LaporanPrintTerimaMasak";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class LaporanStok extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handlePindah(data){
    Axios.get(serverNonMaster + "item/dc/pindahbarang/details", {params: {
      tglFrom: data.tglFrom,
      tglTo: data.tglTo,
      kodeLokasiFrom: data.kodeLokasiFrom,
      kodelokasiTo: data.kodelokasiTo
    }}).then((res)=> localStorage.setItem("LaporanPindahBarang", JSON.stringify(res.data))).catch((err) => gagal("Gagal Mengambil data, Mohon periksa koneksi anda")).then(()=> laporanPrint("Tanggal", `${data.tglFrom} - ${data.tglTo}`, "ADMIN", "2020-12-16", "ADMIN","LAPORAN PERPINDAHAN"))
  }
  handleMutasi(data){
    Axios.get(serverNonMaster + "item/dc/mutasibarang/details", {params: {
      tglFrom: data.tglFrom,
      tglTo: data.tglTo,
      kodeLokasiFrom: data.kodeLokasiFrom,
      kodelokasiTo: data.kodelokasiTo
    }}).then((res)=> localStorage.setItem("LaporanMutasiBarang", JSON.stringify(res.data))).catch((err) => gagal("Gagal Mengambil data, Mohon periksa koneksi anda")).then(()=> laporanPrintMutasi("Tanggal", `${data.tglFrom} - ${data.tglTo}`, "ADMIN", "2020-12-16", "ADMIN","LAPORAN MUTASI BARANG"))
  }
  handleKirimMasak(data){
    data.jenisLaporanKM === "KIRIM MASAK" ?  Axios.get(serverNonMaster + "item/dc/kirimmasak/details", {params: {
      tglFrom: data.tglFrom,
      tglTo: data.tglTo,
      kodeLokasiFrom: data.kodeLokasiFrom,
      kodelokasiTo: data.kodelokasiTo
    }}).then((res)=> localStorage.setItem("LaporanKirimMasak", JSON.stringify(res.data))).catch((err) => gagal("Gagal Mengambil data, Mohon periksa koneksi anda")).then(()=> laporanPrintKirimMasak("Tanggal", `${data.tglFrom} - ${data.tglTo}`, "ADMIN", "2020-12-16", "ADMIN","LAPORAN KIRIM MASAK")) : gagal("DATA KOSONG")
  }
  handleTerimaMasak(data){
    data.jenisLaporanTM === "TERIMA MASAK" ?  Axios.get(serverNonMaster + "item/dc/terimamasak/details", {params: {
      tglFrom: data.tglFrom,
      tglTo: data.tglTo,
      kodeLokasiFrom: data.kodeLokasiFrom,
      kodelokasiTo: data.kodelokasiTo
    }}).then((res)=> localStorage.setItem("LaporanTerimaMasak", JSON.stringify(res.data))).catch((err) => gagal("Gagal Mengambil data, Mohon periksa koneksi anda")).then(()=> laporanPrintTerimaMasak("Tanggal", `${data.tglFrom} - ${data.tglTo}`, "ADMIN", "2020-12-16", "ADMIN","LAPORAN TERIMA MASAK")) : gagal("DATA KOSONG")
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                  <li className="nav-item">
                    <a
                      href="#saldo-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link active"
                    >
                      <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Saldo Awal</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#pindah-tab"
                      data-toggle="tab"
                      aria-expanded="true"
                      className="nav-link "
                    >
                      <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Pindah Barang</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#mutasi-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Mutasi Barang</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#stok-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Stok Per Kategori
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#kartu-stok-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Kartu Stok</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#sales-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">SO Sales</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#jenis-lk-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Jenis LK</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#jenis-rongsok-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Jensi Rongsok</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#kirim-masak-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Kirim Masak / Produksi
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#terima-masak-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Terima Masak / Produksi
                      </span>
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div className="tab-pane show active" id="saldo-tab">
                    <FormStok from="saldoAwal" />
                  </div>
                  <div className="tab-pane" id="pindah-tab">
                    <FormStok from="perpindahanBarang" onSubmit={(data) => this.handlePindah(data)} />
                  </div>
                  <div className="tab-pane" id="mutasi-tab">
                    <FormStok from="kriteria" onSubmit={(data) => this.handleMutasi(data)} />
                  </div>
                  <div className="tab-pane" id="stok-tab">
                    <FormStok from="stokPerKategori" />
                  </div>
                  <div className="tab-pane" id="kartu-stok-tab">
                    <FormStok from="kartuStok" />
                  </div>
                  <div className="tab-pane" id="sales-tab">
                    <FormStok from="soSales" />
                  </div>
                  <div className="tab-pane" id="jenis-lk-tab">
                    <FormStok from="jenisJK" />
                  </div>
                  <div className="tab-pane" id="jenis-rongsok-tab">
                    <FormStok from="jenisJK" />
                  </div>
                  <div className="tab-pane" id="terima-masak-tab">
                    <FormStok from="terimaMasak" onSubmit={(data) => this.handleTerimaMasak(data)} />
                  </div>
                  <div className="tab-pane" id="kirim-masak-tab">
                    <FormStok from="kirimMasak" onSubmit={(data) => this.handleKirimMasak(data)} />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LaporanStok;
