import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const laporanPrint = (
  row1name = "",
  row1isi = "",
  username = "",
  tanggal = "",
  validby = "",
  title = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF();
  let table = JSON.parse(localStorage.getItem("LaporanPindahBarang")) || [];
  var tableRows = [];
  var columTable = [
    "No Pindah",
    "Tipe Barang",
    "Kadar",
    "Kode Jenis",
    "Bruto (Gr)",
    "Harga (%)",
    "Netto (Gr)",
    "Ongkos (Rp)",
  ];
  table.forEach((ticket, i) => {
    ticket.barangDetails.forEach((barang, i) => {
      const barangData = [
        ticket.noBon,
        ticket.kategoriTransaksi,
        barang.kadarCetak,
        barang.kodeJenis,
        barang.bruto,
        barang.kadarTransaksi,
        barang.netto,
        parseFloat(barang.rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(barangData);
    });
  });
  // startY is basically margin-top
  doc.autoTable(columTable, tableRows, { startY: 40 });
  let finalY = doc.lastAutoTable.finalY + 10;
  const date = Date().split(" ");
  // we use a date string to generate our filename.
  const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  doc.text(title, 14, 15);
  doc.setFontSize(10);
  //row 1
  doc.text(row1name, 14, 25);
  doc.text(":", 37, 25);
  doc.text(row1isi, 40, 25);

  //Stamp
  doc.text("User", 160, finalY + 10);
  doc.text(":", 168, finalY + 10);
  doc.text(username, 170, finalY + 10);
  doc.text("Cetak", 160, finalY + 15);
  doc.text(":", 168, finalY + 15);
  doc.text(tanggal, 170, finalY + 15);
  doc.text("Valid", 160, finalY + 20);
  doc.text(":", 168, finalY + 20);
  doc.text(validby, 170, finalY + 20);
  doc.autoPrint();
  doc.save(`${title}_${dateStr}.pdf`);
};

export default laporanPrint;
